*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: black;
    font-family: sans-serif;
}

.alpha{
    position: absolute;
    top: 50%;  
    left: 50%; 
    transform: translate(-50%, -50%);

    width: 70vw;
    background: red;
    border: 2px solid white;
    
    video{
        width: 100%;
        
    }
}